import { useCallback, useEffect, useState } from "react";
import { ViberClient } from "messaging-api-viber";

const viberToken = "4dd7486adb67d0e5-1d2b253e4cb08827-4229ac22813666e";

function App() {
  const [value, setValue] = useState("");
  const getAccountInfo = () => {
    fetch("https://addcontactt.herokuapp.com/get_account_info", {
      method: "GET",
    })
      .then((res) => res.json())
      .then((data) => console.log(data));
  };
  useEffect(() => {
    fetch("https://addcontactt.herokuapp.com/set_webhook", {
      method: "GET",
    })
      .then((res) => res.json())
      .then((data) => console.log(data))
      .catch((e) => console.log(e.message));
  }, []);
  setTimeout(() => {
    getAccountInfo();
  }, 3000);
  const handleSend = useCallback(() => {
    setValue("");
    fetch("http://localhost:8080/send_message", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ text: value }),
    })
      .then((res) => res.json())
      .then((data) => console.log(data));
  }, [value]);

  return (
    <div>
      <input value={value} onChange={(e) => setValue(e.target.value)} />
      <button onClick={handleSend}>Send</button>
    </div>
  );
}

export default App;
